.page
{
    .sidebar
    {
        width: 250px;
        position: fixed;
        top:0;
        left: 0;
        height: 100%;
        background-color: #eee;
        padding: 15px;
        float:right;
        transition: .3s;
        &.no-sidebar{
            left: -250px;
        }
    }
}